.popup {
  display: flex;
  position: fixed;
  background: rgba(0, 0, 0, .9);
  height: 100%;
  width: 100%;
  left: 0;
  justify-content: center;
  align-items: center;
  top: 0;
  z-index: -1;
  opacity: 0;
  transition: z-index 1s, opacity 1s;
}

@media screen and (min-width: 320px) and (max-width: 900px){
  .popup {
    max-width: 900px;
    left: auto;
  }
}