.popup__textarea {
  max-width: 358px;
  min-width: 358px;
  max-height: 35vh;
  min-height: 60px;
  border: solid 1px;
  border-radius: 10px;
  text-indent: 5px;
}

@media screen and (min-width: 320px) and (max-width: 900px){
  .popup__textarea {
    max-width: 230px;
    min-width: 230px;
  }
}