.popup__header {
  padding: 34px 64px 0 36px;
  margin: 0;
  font-family: Play, Arial, Helvetica, sans-serif;
  font-size: 24px;
  text-align: left;
}
@media screen and (min-width: 320px) and (max-width: 900px){
  .popup__header {
    padding: 25px 22px 0 22px;
    font-size: 18px;
    line-height: 22px;
  }
}